/* src/styles/Login.css */
body.login-background {
  background-image: url('https://i.ibb.co/cJ9zpVz/Dashboard.png');
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

body {
  margin: 0;
  padding: 0;
}

.login-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding-right: 20px;
}

.login-form-wrapper {
  width: 100%;
  max-width: 400px;
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
